//Basic body pluss call ui 
@import'_ui';
//fonts and main colors  must be type here 

// @import url('https://fonts.googleapis.com/css?family=Nanum+Gothic:400,700,800&display=swap');
@import url('https://fonts.googleapis.com/css?family=Inconsolata&display=swap');
// $custom-font:'Nanum Gothic'; 
$custom-font:'Inconsolata', monospace;
//variables to complete
$bg:white;
$tnormalcolor:black;




//base  for all the site 

html, body{
	position: absolute;
  	background-color: $bg;
  	margin: 0px;
  	padding: 0px;
  	overflow: hidden;
	color:$tnormalcolor;
	width: 100%;
	height: 100%;
	font-family: $custom-font;
}


canvas{
	display: block;
	z-index:0;
}


.wrapper{
	position: absolute;
	font-family: $custom-font;
	z-index: 5;
}

.contents{
	margin-top: 30vh;
	margin-left:50vh;
	background-color: white;
	text-align: center;
	opacity: 0.9555;
	color: black;
	width: 500px;
	border-color:#303030;
	@include border-width(0.25em 0.2em 1em 0.5em);
	border-style:outset;
	transform-style: preserve-3d;
	perspective: 270px;
	perspective-origin: top;
}

